/* LOADING SCREEN */
.loading {
  background-color: #00000A;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  font-family: "Fredoka One", cursive;
  font-size: 40px;
  letter-spacing: -2px;
  color: #FFFFFF;
  margin-bottom: 10px;
}

/* LANDING SCREEN */
.landing {
  background-color: #00000A;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.landing:before {
  content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.15;
    background-image: url('https://res.cloudinary.com/michelleytlock/image/upload/v1593523094/mediabox-assets/landingbg_orkmj8.jpg');
    background-repeat: no-repeat;
    background-position: 50% 70%;
    -ms-background-size: cover;
    background-size: cover;
}

.landing-content {
  width: 100vw;
  margin: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
  position: relative
}

.intro-text {
  color: white;
  font-size: 26px;
  font-weight: 700;
  line-height: 30px;
}

.signup-button {
  font-weight: 700;
}

.login-button {
  position: absolute;
  top: 0;
  right: 0
}

/* SIGN UP & LOG IN PAGE */
.signup-page {
  margin: 40px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.close-button {
  border: 0;
  background-color: white;
  position: absolute;
  top: 0;
  right: 0
}

.title {
  font-weight: 900;
}

.signup-page input {
  border-bottom: 1px solid #4A6670;
  margin-top: -10px;
  margin-bottom: 10px;
  padding: 0;
}

.last-field input {
  margin-bottom: 30px;
}
/* $red: #e20f0f;
$grey-dark: #4A6670;
$white: #FFFFFF;
$black: #00000A; */

.onboarding-page {
  margin: 15px 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.progress-bar {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 15px
}

.progress-percentage {
  font-weight: 700;
  margin-bottom: 3px
}

.rater {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rating-buttons {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0 20px 0;
}

.circle-buttons {
  border-radius: 100%;
}

.media-image {
  width: 85%
}

.instructions {
  background-color: #4A6670;
  color: white;
  width: 100vw;
  padding: 15px 30px;
  text-align: center;
}
/* $red: #e20f0f;
$grey-dark: #4A6670;
$white: #FFFFFF;
$black: #00000A; */

/* HOME */
.home-page {
  margin: 20px 30px 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* RATER */
.rater {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.rating-buttons {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 8px 0 15px 0;
}

.circle-buttons {
  border-radius: 100%;
}

.media-image {
  width: 75%;
}

.action-buttons {
  display: flex;
  flex-direction: row;
}

.action-buttons button {
  margin-right: 10px;
}

/* NAVBAR */
.myNav {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 700;
  height: 60px;
  padding: 0 30px;
}

.navbar-item {
  color: white;
  text-transform: uppercase;
  font-size: 19px;
}

/* FILTERS */
.main-filter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 5px 0 20px 0
}

.media-filter {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.search-filter {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.search-filter input {
  width: 85%;
  margin-right: 10px;
  border-bottom: 1px solid #4A6670;
  padding: 0;
}

.random-medias-toggler {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.random-medias-toggler button {
  margin-top: 15px;
  margin-right: 10px
}

/* WATCHLIST */
.watchlist-page {
  margin: 20px 20px 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.watchlist-placeholder {
  text-align: center;
}

/* LIST */
.list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 10px 0 70px 0;
}

.list-item {
  width: 46%;
  margin: 0 6px 15px 6px
}

.list-item-title {
  color: black;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}

/* SEARCH RESULTS */
.search-results-page {
  margin: 0px 20px 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.back-button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 0;
  background-color: white;
  padding: 0
}

.header {
  margin: 20px 25px 0 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header-type {
  color: #e20f0f;
}
